import React from 'react'
import {
  Box,
  Button,
  Grommet,
  Image,
} from 'grommet'

function shuffle(array) {
  var currentIndex = array.length,
    temporaryValue,
    randomIndex

  // While there remain elements to shuffle...
  while (0 !== currentIndex) {
    // Pick a remaining element...
    randomIndex = Math.floor(
      Math.random() * currentIndex
    )
    currentIndex -= 1

    // And swap it with the current element.
    temporaryValue = array[currentIndex]
    array[currentIndex] =
      array[randomIndex]
    array[randomIndex] = temporaryValue
  }

  return array
}

class Dogs extends React.Component {
  state = {
    breeds: {},
    image: '',
    currentBreed: '',
    alternatives: [],
    background: 'dark-2',
    loading: true,
  }

  componentDidMount() {
    this.getRandomImage()
    fetch(
      'https://dog.ceo/api/breeds/list/all'
    )
      .then(response => response.json())
      .then(result => {
        this.setState({
          breeds: Object.keys(
            result.message
          ),
        })
      })
      .catch(err => console.log(err))
  }

  checkAnswer = answer => {
    const { currentBreed } = this.state
    let color
    if (currentBreed === answer) {
      color = 'green'
    } else {
      color = 'red'
    }

    this.setState({ background: color })
    this.getRandomImage()
  }

  setAlternatives = () => {
    const { breeds } = this.state

    const rand1 =
      Math.floor(
        Math.random() *
          (breeds.length - 0 + 1)
      ) + 0
    const rand2 =
      Math.floor(
        Math.random() *
          (breeds.length - 0 + 1)
      ) + 0

    console.log(breeds[rand1])

    this.setState(prevState => {
      const alternatives = [
        ...prevState.alternatives,
      ]
      alternatives.push(breeds[rand1])
      alternatives.push(breeds[rand2])

      return {
        alternatives: shuffle(
          alternatives
        ),
      }
    })
  }

  getRandomImage = () => {
    this.setState({ alternatives: [] })

    fetch(
      'https://dog.ceo/api/breeds/image/random'
    )
      .then(response => response.json())
      .then(result => {
        const url = new URL(
          result.message
        )
        const currentBreed = url.pathname.split(
          '/'
        )[2]

        this.setState(prevstate => {
          console.log(
            prevstate.alternatives
          )
          const alternatives = [
            ...prevstate.alternatives,
          ]
          alternatives.push(
            currentBreed
          )
          return {
            image: url.href,
            currentBreed,
            alternatives,
            loading: false,
          }
        })
        this.setAlternatives()
      })
  }

  render() {
    const {
      breeds,
      image,
      currentBreed,
      alternatives,
      background,
      loading,
    } = this.state
    return (
      <Grommet>
        <Box
          fill
          height="xsmall"
          background={background}
        >
          {image.length > 0 && (
            <Box>
              <Box height="small">
                <Image
                  fit="contain"
                  src={image}
                />
              </Box>
              {console.log(
                JSON.stringify(
                  alternatives
                )
              )}
              <Box
                direction="row"
                justify="center"
                wrap
              >
                {alternatives.length >
                  0 &&
                  alternatives.map(
                    (
                      alternative,
                      index
                    ) => {
                      return (
                        <Button
                          onClick={() =>
                            this.checkAnswer(
                              alternative
                            )
                          }
                        >
                          <Box
                            margin="small"
                            key={index}
                          >
                            {
                              alternative
                            }
                          </Box>
                        </Button>
                      )
                    }
                  )}
              </Box>
            </Box>
          )}
        </Box>
      </Grommet>
    )
  }
}

export default Dogs
